import * as React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { graphql } from "gatsby"
import PlainTile from "../components/common/plainTile"
import TileTwoColumnImageLeft from "../components/common/tileTwoColumnImageLeft"
import TileThreeColumn from "../components/common/tileThreeColumn"
import IntroTileWithImage from "../components/common/introTileWithImage"
import { getImage } from "gatsby-plugin-image"

const IndexPage = ({ data }) => {
  const sections = data.allMarkdownRemark.nodes

  const getSection = section => {
    if (section.frontmatter.sectionType === "tileThreeColumn") {
      return (
        <TileThreeColumn key={section.id}
          title={section.frontmatter.title}
          subtitle={section.frontmatter.subtitle}
          tileThreeDescription={section.frontmatter.tileThreeDescription}
          tileThreeImage={getImage(section.frontmatter.tileThreeImage)}
          tileThreeTitle={section.frontmatter.tileThreeTitle}
          tileTwoDescription={section.frontmatter.tileTwoDescription}
          tileTwoImage={getImage(section.frontmatter.tileTwoImage)}
          tileTwoTitle={section.frontmatter.tileTwoTitle}
          tileOneTitle={section.frontmatter.tileOneTitle}
          tileOneImage={getImage(section.frontmatter.tileOneImage)}
          tileOneDescription={section.frontmatter.tileOneDescription}
        />
      )
    } else if (section.frontmatter.sectionType === "tileTwoColumnImageLeft") {
      return (
        <TileTwoColumnImageLeft key={section.id}
          title={section.frontmatter.title}
          subtitle={section.frontmatter.subtitle}
          headerImage={getImage(section.frontmatter.headerImage)}
          link={section.frontmatter.link}
          linkName={section.frontmatter.linkName}
          tileDescription={section.frontmatter.tileDescription}
        />
      )
    } else if (section.frontmatter.sectionType === "introTileWithImage") {
      return (
        <IntroTileWithImage key={section.id}
          title={section.frontmatter.title}
          subtitle={section.frontmatter.subtitle}
          headerImage={getImage(section.frontmatter.headerImage)}
          link={section.frontmatter.link}
          linkName={section.frontmatter.linkName}
          tileDescription={section.frontmatter.tileDescription}
        />
      )
    } else if (section.frontmatter.sectionType === "tile") {
      return (
        <PlainTile key={section.id}
          title={section.frontmatter.title}
          subtitle={section.frontmatter.subtitle}
          tileDescription={section.frontmatter.tileDescription}
        />
      )
    }
  }
  return (
    <Layout>
      <Seo title="Home" />
      {/* <h1>Hi people</h1>
    <p>Welcome to your new Gatsby site.</p>
    <p>Now go build something great.</p>
    <StaticImage
      src="../images/gatsby-astronaut.png"
      width={300}
      quality={95}
      formats={["auto", "webp", "avif"]}
      alt="A Gatsby astronaut"
      style={{ marginBottom: `1.45rem` }}
    />
    <p>
      <Link to="/page-2/">Go to page 2</Link> <br />
      <Link to="/using-typescript/">Go to "Using TypeScript"</Link>
    </p> */}
      {sections && sections.map(section => getSection(section))}
    </Layout>
  )
}

export default IndexPage

export const pageQuery = graphql`
  query AllSectionsForHomePage {
    allMarkdownRemark(
      filter: { frontmatter: { type: { eq: "data" }, for: { eq: "home" } } }
      sort: { fields: frontmatter___order, order: ASC }
    ) {
      nodes {
        id
        frontmatter {
          title
          subtitle
          description
          order
          link
          linkName
          tileDescription
          sectionType
          tileThreeDescription
          tileThreeImage {
            childImageSharp {
              gatsbyImageData
            }
          }
          tileThreeTitle
          tileTwoDescription
          tileTwoImage {
            childImageSharp {
              gatsbyImageData
            }
          }
          tileTwoTitle
          headerImage {
            childImageSharp {
              gatsbyImageData
            }
          }
          tileOneTitle
          tileOneImage {
            childImageSharp {
              gatsbyImageData
            }
          }
          tileOneDescription
        }
      }
    }
  }
`
